body {
    margin: 0;
    padding: 0;
}

.flex-row {
    display: flex;
    flex-direction: row;
}

.flex-column {
    display: flex;
    flex-direction: column;
}

.flex-grow {
    flex-grow: 1;
}

.center-vertical {
    align-items: center;
}

.highlight {
    color: green;
    font-style: italic;
}